import WangxiaoTable from "./components/wangxiao-table/index.js";
import WangxiaoBtn from "./components/wangxiao-btn/index.js";
import WangxiaoSidebar from "./components/wangxiao-sidebar/index.js";
import WangxiaoStep from "./components/wangxiao-step/index.js";
import WangxiaoUpload from "./components/wangxiao-upload/index.js";
import WangxiaoEditor from "./components/wangxiao-editor/index.js";

// 
const components = [WangxiaoTable, WangxiaoBtn, WangxiaoSidebar, WangxiaoStep, WangxiaoUpload, WangxiaoEditor];

// 
// const directives = [xxxx]
// 
// const filters = [xxxx]

//  install Vue 
const install = Vue => {
    // 
    if (install.installed) return;
    install.installed = true;
    // 
    components.map((component) => Vue.component(component.name, component));

    // 
    // directives.map(directives => Vue.use(directives))

    // 
    // filters.map(filters => Vue.use(filters))
};

//  Vue 
if (typeof window !== "undefined" && window.Vue) {
    install(window.Vue);
}
export { WangxiaoTable, WangxiaoBtn, WangxiaoSidebar, WangxiaoStep, WangxiaoUpload, WangxiaoEditor } //  
export default {
    install,
    //  install  Vue.use()
    WangxiaoTable,
};