/**
 *  url 
 * @param url
 * @param fileName 
 * @param determine  ( true )
 */
function downLoadFile(url, fileName, determine) {
    let fileNames;
    /** fileName  */
    if (fileName) {
        const suffixName = fileUrlType(fileName || "", determine || false);
        const suffixUrl = fileUrlType(url || "", determine || false);
        /** */
        if (suffixName) {
            fileNames = fileName;
        }
        else if (suffixUrl) {
            /** url  */
            fileNames = `${fileName}.${suffixUrl}`;
        }
        else {
            /**    */
            console.error("Please check if your link or file name is a valid suffix. If so, please set the third parameter in the downLoadFile function to true!");
        }
    }
    else {
        /** url  */
        const suffixUrl = fileUrlType(url || "", determine || false);
        if (suffixUrl) {
            fileNames = url.slice(url.lastIndexOf('/') + 1);
        }
        else {
            console.error("Please check if your link or file name is a valid suffix. If so, please set the third parameter in the downLoadFile function to true!");
        }
    }
    fetch(url).then((res) => res.blob())
        .then((blob) => {
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = fileNames;
        document.body.appendChild(link);
        link.click();
        URL.revokeObjectURL(link.href);
        document.body.removeChild(link);
    });
}
exports.downLoadFile = downLoadFile;
